<!-- by 添少 -->
<template>
  <fixed-layout>
    <template slot="header">
      <topbar>
        <template slot="before">
          <b class="iconfont icon-back" @click="$router.go(-1)"></b>
        </template>
        编辑行程
        <template slot="after">
          <button class="btn" @click="handNext">发布</button>
        </template>
      </topbar>
    </template>
    <section class="m-edit">
      <section class="edit-hd">
        <section class="input-box">
          <input v-model="title" style="font-size: 24px;" type="text" placeholder="请输入标题"/>
        </section>
        <section class="input-box">
          <textarea v-model="desc" placeholder="请输入摘要，建议简明扼要突出行程亮点，不超过25字。"></textarea>
        </section>
        <section id="toolbar-container">
          <select class="ql-size">
            <!-- Note a missing, thus falsy value, is used to reset to default -->
            <option selected></option>
            <option value="large"></option>
            <option value="huge"></option>
          </select>
          <button class="ql-bold" data-toggle="tooltip"></button>
          <button class="ql-italic" data-toggle="tooltip"></button>
          <button class="ql-underline" data-toggle="tooltip"></button>
          <button class="ql-strike" data-toggle="tooltip"></button>
          <button class="ql-image" data-toggle="tooltip"></button>
        </section>
      </section>
      <section class="edit-bd">
        <div id="editor"></div>
      </section>
    </section>
  </fixed-layout>
</template>
<script>
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'
  import Quill from 'quill'
  import 'quill/dist/quill.snow.css'

  let quill = null
  export default {
    name: '',
    components: {
      FixedLayout,
      Topbar
    },
    props: {},
    data () {
      return {
        cover: '',
        title: '',
        desc: '',
        content: ''
      }
    },
    watch: {},
    computed: {},
    created () {
      this.init()
    },
    mounted () {
      quill = new Quill('#editor', {
        modules: {
          toolbar: {
            container: '#toolbar-container'
          }
        },
        placeholder: '请输入正文',
        theme: 'snow'
      })
      console.log(quill)
    },
    methods: {
      init () {
      },
      initData () {},
      handNext () {
        this.content = quill.container.firstChild.innerHTML
        this.$store.commit(this.$actionType.write.set_article_data, {
          title: this.title,
          desc: this.desc,
          content: this.content
        })
        this.$router.push({
          path: 'preview'
        })
      }
    }
  }
</script>
<style lang="scss">

</style>
<style lang="scss" scoped>
  .fixed-layout {
    background-color: #fff;
  }

  .input-box {
    padding: 10px;
    border-bottom: dashed #d7d7d7 1px;
    input,
    textarea {
      width: 100%;
      border-color: transparent;
    }
  }

  .m-edit {
    height: 100%;
    display: flex;
    flex-direction: column;
    > .edit-hd {
      flex: 0 0 1;
    }
    > .edit-bd {
      flex: 1;
      overflow: hidden;
      overflow-y: scroll;
      -webkit-overflow-scrolling: touch;
    }
  }

  #editor {
    font-size: 16px;
  }
</style>



